import Vue from 'vue';
import VueI18n, { DateTimeFormats, LocaleMessages } from 'vue-i18n';

Vue.use(VueI18n);

function loadLocaleMessages(): LocaleMessages {
    const locales = require.context('../locales/messages', true, /[A-Za-z0-9-_,\s]+\.json$/i);
    const messages: LocaleMessages = {};
    locales.keys().forEach((key) => {
        const matched = key.match(/([A-Za-z0-9-_]+)\./i);
        if (matched && matched.length > 1) {
            const locale = matched[1];
            messages[locale] = locales(key);
        }
    });
    return messages;
}

function loadLocaleDateTimeFormats(): DateTimeFormats {
    const locales = require.context('../locales/dateTimeFormats', true, /[A-Za-z0-9-_,\s]+\.json$/i);
    const dateTimeFormats: DateTimeFormats = {};
    locales.keys().forEach((key) => {
        const matched = key.match(/([A-Za-z0-9-_]+)\./i);
        if (matched && matched.length > 1) {
            const locale = matched[1];
            dateTimeFormats[locale] = locales(key);
        }
    });
    return dateTimeFormats;
}

export default new VueI18n({
    locale: process.env.VUE_APP_I18N_LOCALE || 'en',
    fallbackLocale: process.env.VUE_APP_I18N_FALLBACK_LOCALE || 'en',
    messages: loadLocaleMessages(),
    dateTimeFormats: loadLocaleDateTimeFormats()
});
